import React, { Component } from 'react'
import Card from './Card'

export class All extends Component {
    state = {
        Cards : [
            {id : 1 , type : "a"},
            {id : 2 , type : "b"},
            {id : 3 , type : "c"},
            {id : 4 , type : "d"},
            {id : 5 , type : "e"},
            {id : 6 , type : "f"},
            {id : 7 , type : "g"},
            {id : 8 , type : "h"},
            {id : 9 , type : "h"},
            {id : 10 , type : "a"},
            {id : 11 , type : "b"},
            {id : 12 , type : "c"},
            {id : 13 , type : "d"},
            {id : 14 , type : "e"},
            {id : 15 , type : "f"},
            {id : 16 , type : "g"}
        ],
        clicker: 0,
        one: null,
        two: null,
        oneId: null,
        twoId: null,
        enable : false
    }

    handleClick = (item, type) => {
        this.rotate(item);
        if (this.state.clicker === 0) 
            this.next(item, type)
        else 
            this.check(item, type)
    }
    next = (item, type) => {
        this.setState({clicker : 1});
        const one = type;
        this.setState({one})
        const oneId = item;
        this.setState({oneId})
    }
    check = (item, type) => {
        this.setState({clicker : 0});
        const two = type;
        this.setState({two})
        const twoId = item;
        this.setState({twoId})
        this.setState({enable : true})
        this.handleWin(type, twoId);
    }
    handleWin = (two, twoId) => {
        this.setState({enable : true})
        const { one, oneId } = this.state;
        setTimeout(() => {
            this.setState({enable : false})
            if (one === two) {
            console.log("win")

        }
        else {
            console.log("lose")
            this.unRotate(oneId)
            this.unRotate(twoId)
        }
        }, 1000);
        
    }
    rotate = (item) => {
        document.getElementById(`back${item}`).style.transform= "rotateY(0deg)";
        document.getElementById(`front${item}`).style.transform= "rotateY(180deg)";
    }
    unRotate = (item) => {
        document.getElementById(`back${item}`).style.transform= "rotateY(-180deg)";
        document.getElementById(`front${item}`).style.transform= "rotateY(0deg)";
    }
    render() {
        return (
            <div className="cards">
                {this.state.Cards.map(card=>
                    <Card 
                        key={card.id} 
                        id={card.id} 
                        onClick={this.handleClick} 
                        type={card.type}
                        enable={this.state.enable}
                    /> 
                )}
            </div>
        )
    }
}

export default All


