import React, { Component } from 'react'

export class Card extends Component {
    render() {
        const {id , onClick, type, enable} = this.props;
        const cls = `card card${id}`;
        const frontCls = `sides front front${id}`;
        const backCls = `sides back back${id}`;

        return (
            <div className={cls}>
                <button     
                    id={`front${id}`} 
                    onClick={()=>onClick(id,type)}
                    className={frontCls}
                    disabled={enable}
                >
                    {type}
                </button>
                <button id={`back${id}`} className={backCls}></button>
            </div>
        )
    }
}

export default Card
